import React from "react";
import Hello from "./Hello";
import { Link } from "react-router-dom";

class Login extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      user: {
        email: "",
        pass: ""
      }
    };

    this.updateValues = this.updateValues.bind(this);
  }

  updateValues(e) {
    let { user } = this.state;
    let id = e.target.id;
    let value = e.target.value;
    let updatedInfo = { [id]: value };

    user = { ...user, ...updatedInfo };
    this.setState({ user });
  }

  componentWillReceiveProps(nextProps) {
    console.log("Login screen receive props - " + Math.random(), nextProps);
  }



  render() {
    let { user } = this.state;
    let { onclick, currentUser, onclickfacebook } = this.props;

    if (currentUser) return <Hello />;

    if (currentUser === undefined) return <h1></h1>;

    //alert(currentUser);

    return (
      <div className="main" >
        <div className="loginWrapper" >
          <h3>{this.props.title}</h3>
          <form className = "login" onChange={this.updateValues}>
            <div>
              <span>Email </span>
              <input id="email" type="email" />
            </div>
            <div>
              <span>Password </span>
              <input id="pass" type="password" />
            </div>
            <div style={{ marginTop: 15 }}>
              <span> </span>
              <input
                type="button"
                className="btn"
                value="Login"
                onClick={() => {
                  onclick(user);
                }}
              />
            </div>
          </form>
          {/*<Link to={'/register'}> Register </Link>
            <div style={{ "marginTop": 15 }}>
              <span>  </span>
              <input type='button' value="Facebook" onClick={onclickfacebook} />
            </div>*/}
        </div>
      </div>
    );
  }
}

export default Login;



