import React, { Component } from "react";
import { render } from "react-dom";
//import Register from './components/Register';
import Login from "./components/Login";
import Nav from "./components/Nav";
import Pages from "./components/Pages";
import Portfolio from "./components/Portfolio";
import Blog from "./components/Blog";
import { Router, Route, Switch, Redirect } from "react-router-dom";
import createBrowserHistory from "history/createBrowserHistory";
import firebase from "./firebase";
import "./index.css";

const history = createBrowserHistory();

let auth = firebase.auth;
const provider = new firebase.auth.FacebookAuthProvider();

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: undefined,
      isLogout: false,
      random: null
    };

    this.onLogout = this.onLogout.bind(this);
  }

  componentDidMount() {
    auth().onAuthStateChanged(user => {
      if (user) {
        console.log("user logged in", user);
        this.setState({ user });
      } else {
        console.log("user not logged in", user);
        this.setState({ user: null });
      }
    });
  }

  onLoginFacebook() {
    auth().signInWithPopup(provider);
  }

  onLoginEmail(user) {
    //console.log(user);
    auth()
      .signInWithEmailAndPassword(user.email, user.pass)
      .then(snapshot => {
        console.log("login SUCCESS");
        console.log(snapshot);
      })
      .catch(err => {
        console.log("login FAILED");
        console.log(err);
        alert(err);
      });
  }

  onRegisterEmail(user) {
    //console.log(user);
    auth()
      .createUserWithEmailAndPassword(user.email, user.pass)
      .then(snapshot => {
        console.log("regisration SUCCESS");
        console.log(snapshot);
      })
      .catch(err => {
        console.log("regisration FAILED");
        console.log(err);
      });
  }

  onLogout() {
    auth()
      .signOut()
      .then(() => {
        console.log("logout success");
        this.setState({ isLogout: true });
      })
      .catch(err => {
        console.log("logout failed");
      });
  }

  render() {
    let { user } = this.state;

    return (
      <div>
        <Router history={history}>
          <div className="container">
            <div className="header">
              <h2>eka.risyana.pribadi</h2>
              <div>{user ? user.email : ""}</div>
            </div>
            <Nav onlogout={this.onLogout} user={user} />
            <Switch>
              <Route
                exact
                path={"/"}
                component={() => {
                  return <Portfolio />;
                }}
              />
              {/*<Route 
                path={'/register'} 
                component={()=>{return <Register 
                                         title="Register" 
                                         onclick={this.onRegisterEmail}/> }} />*/}
              <Route component={() => <h1>404: not found</h1>} />
            </Switch>
            <div className="footer">
              <p>eka.risyana.pribadi</p>
            </div>
          </div>
        </Router>
      </div>
    );
  }
}

render(<App />, document.getElementById("root"));



