import React from "react";
import { Link, Switch, Route } from "react-router-dom";
import firebase from "../firebase";
import Button from "./Button";

let pagesRef = null;
//let portfolio = null;
class PagesList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      pages: null
    };
    this.toggleHidden = this.toggleHidden.bind(this);
  }

  componentDidMount() {
    this.getData();
  }

  componentWillUnmount() {
    this.setState({ pages: null });
  }

  getData() {
    pagesRef = firebase.database().ref("pages");
    let { pages } = this.state;
    pagesRef.on("value", data => {
      pages = data.val();
      this.setState({ pages });
    });
  }

  toggleHidden(key, hidden) {
    console.log("curr:" + hidden);
    hidden = hidden === 0 ? 1 : 0;
    key = `${key}/hidden`;
    pagesRef.update({
      [key]: hidden
    });
    console.log("new:" + hidden);
  }

  render() {
    let { pages } = this.state;
    if (!pages)
      return (
        <div>
          {" "}
          <Link to={"/pages/add"}>
            Add<br />
          </Link>loading pages..
        </div>
      );

    return (
      <div>
        <Link to={"/pages/add"}>Add</Link>
        <hr className="heading" />
        <div>
          <div className="record head">
            <span className="column pIdx">No</span>
            <span className="column pTitle">Title</span>
            <span className="column pContent">Content</span>
            <span className="actionButton">Action</span>
          </div>
          <hr className="heading" />

          {Object.keys(pages).map((key, idx) => {
            return (
              <div className="record" key={idx}>
                <span className="column pIdx">{idx + 1}</span>
                <span className="column pTitle">{pages[key].title}</span>
                <span className="column pContent">{pages[key].content}</span>
                <span className="actionButton">
                  <Link to={`/pages/edit/${key}`}>edit</Link>
                </span>
                <span className="actionButton">
                  <Link to={`/pages/delete/${key}`}>delete</Link>
                </span>
                <span className="actionButton">
                  <Button
                    onClick={() => {
                      this.toggleHidden(key, pages[key].hidden);
                    }}
                    text={pages[key].hidden === 0 ? "hide" : "show"}
                  />
                </span>
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

export default PagesList;



