import React from "react";
import { Link, Switch, Route } from "react-router-dom";
import firebase from "../firebase";
import Button from "./Button";

let portfolioRef = null;
//let portfolio = null;
class PortfolioList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      portfolio: null
    };

    this.toggleHidden = this.toggleHidden.bind(this);
  }

  componentDidMount() {
    this.getData();
  }

  componentWillUnmount() {
    this.setState({ portfolio: null });
  }

  getData() {
    portfolioRef = firebase.database().ref("portfolio");
    let { portfolio } = this.state;
    portfolioRef.on("value", data => {
      portfolio = data.val();
      console.log("TEST EXEC");
      this.setState({ portfolio });
    });
  }

  toggleHidden(key, hidden) {
    console.log("curr:" + hidden);
    hidden = hidden === 0 ? 1 : 0;
    key = `${key}/hidden`;
    portfolioRef.update({
      [key]: hidden
    });
    console.log("new:" + hidden);
  }

  render() {
    let { portfolio } = this.state;
    if (!portfolio) return <div>loading..</div>;

    return (
      <div>
        <hr className="heading" />
        <div>
          <div className="record head">
            <span className="column pIdx">No</span>
            <span className="column pName">Project</span>
            <span className="column pDesc">Description</span>
            <span className="column pTech">Technology</span>
            <span className="column pGithub">Github</span>
          </div>
          <hr className="heading" />
          {Object.keys(portfolio).map((key, idx) => {
            return (
              <div className="record" key={idx}>
                <span className="column pIdx">{idx + 1}</span>
                <span className="column pName">
                  <a href={portfolio[key].url} target="_blank">
                    {portfolio[key].name}
                  </a>
                </span>
                <span className="column pDesc">{portfolio[key].desc}</span>
                <span className="column pTech">{portfolio[key].tech}</span>
                <span className="column pGithub">
                  <a href={portfolio[key].github}>{`Github`}</a>
                </span>
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

export default PortfolioList;



