import React from "react";
import { Redirect } from "react-router-dom";

class Blog extends React.Component {
  render() {
    let { user } = this.props;
    if (!user) return <Redirect to={"/"} />;

    return (
      <div className="main">
        <h2>Blog</h2>
      </div>
    );
  }
}

export default Blog;



