import React from 'react';
import { Link } from 'react-router-dom';
import { Redirect } from 'react-router-dom';
import firebase from '../firebase';

var portfolioRef = null;

class PortfolioForm extends React.Component{
  constructor(props){
    super(props);

    this.state={
      curPortfolio:{
        desc: "",
        github: "",
        hidden: 0,
        img: "",
        name: "",
        tech: "",
        url: "",
      },
      redirect:false,
      curKey: null,
      mode:null,
    }

    this.updateValues = this.updateValues.bind(this);
    this.addNew = this.addNew.bind(this);
    this.updateData = this.updateData.bind(this);
    this.deleteData = this.deleteData.bind(this);
  }

  updateValues(e) {

    let { curPortfolio } = this.state;
    let id = e.target.id;
    let value = e.target.value;
    let updatedInfo = {[id]: value};

    curPortfolio = {...curPortfolio, ...updatedInfo}
    this.setState({ curPortfolio } )
  }

  componentWillMount(){
    let mode = this.props.match.params.mode;
    let curKey  = this.props.match.params.id;
    this.setState({mode,curKey});
  }

  componentDidMount(){
    
    portfolioRef = firebase.database().ref("portfolio");
    let { curKey } = this.state;
    
    if(curKey){  // edit or delete
      portfolioRef.orderByKey().equalTo(curKey).on("value",(snapshot)=>{
        let data = snapshot.val();
        if(data){
          data = data[curKey]
          this.setState({curPortfolio:data})
        }  
      }, (err)=>{
        console.log("error get key: ",err,curKey)
      })
    }

  }

  

  addNew() {
    let { curPortfolio } = this.state;
    
    if(this.refs.form){
      this.setState({ redirect: true })
    }  
    portfolioRef.push(curPortfolio);
  }
  
  
  updateData(){
    let { curKey, curPortfolio } = this.state;
    
    let updatedData = {[curKey]:curPortfolio};
    if(this.refs.form){
      this.setState({ redirect: true })
    }  
    portfolioRef.update(updatedData);
  }
  
  deleteData(){
    let { curKey } = this.state;
    
    if(this.refs.form){
      this.setState({ redirect: true, curKey:null })
    }  
    portfolioRef.child(curKey).remove();
  }

  render(){
    let { curPortfolio,redirect, mode } = this.state;
    let onclick;
    if(mode === "add"){
      onclick = this.addNew;
    }else if(mode === "edit"){
      onclick = this.updateData;
    }else if(mode === "delete"){
      onclick = this.deleteData;
    }
    
    if(redirect) return <Redirect to={'/portfolio'} />
    
    console.log("curr:",curPortfolio);
    
    if(!curPortfolio || !mode) return <p>loading..</p>;

    return(
      <div ref="form">
        <form className="portfolioForm" onChange={this.updateValues}>
          <div> 
            <span>Name  </span>
            <input id='name' type='text' value = {curPortfolio.name} />
          </div>
          <div>
            <span>Description </span>
            <input id='desc' type='text' value={curPortfolio.desc} />
          </div>
          <div>
            <span>Tech </span>
            <input id='tech' type='text' value={curPortfolio.tech} />
          </div>
          <div>
            <span>URL (Live) </span>
            <input id='url' type='text' value={curPortfolio.url} />
          </div>
          <div>
            <span>URL (Source) </span>
            <input id='github' type='text' value={curPortfolio.github} />
          </div>
          <div>
            <span>Image </span>
            <input id='img' type='text' value={curPortfolio.img} />
          </div>
          <div>
            <span>Hidden </span>
            <select id='hidden'>
              <option value={0}>No</option>
              <option value={1}>Yes</option>
            </select>
          </div>
          <div style={{"marginTop":15}}>
            <span>  </span>
            <input type='button' className={`btn ${mode}`} value={mode.toUpperCase()} onClick={onclick} />
          </div>
        </form>
        <Link to={'/portfolio'}> Cancel </Link>
        
      </div>
    )
  }
}

export default PortfolioForm;


