import React from 'react';
import { Link } from 'react-router-dom';
import { Redirect } from 'react-router-dom';
import firebase from '../firebase';
import marked from 'marked';

var pagesRef = null;

class PagesForm extends React.Component{
  constructor(props){
    super(props);

    this.state={
      curPages:{
        title: "",
        content: "",
        hidden: 0,
      },
      redirect:false,
      curKey: null,
      mode:null,
    }

    this.updateValues = this.updateValues.bind(this);
    this.addNew = this.addNew.bind(this);
    this.updateData = this.updateData.bind(this);
    this.deleteData = this.deleteData.bind(this);
    this.onTypeContent = this.onTypeContent.bind(this);

  }

  updateValues(e) {

    let { curPages } = this.state;
    let id = e.target.id;
    let value = e.target.value;
    let updatedInfo = {[id]: value};

    curPages = {...curPages, ...updatedInfo}
    this.setState({ curPages } )
  }

  componentWillMount(){
    let mode = this.props.match.params.mode;
    let curKey  = this.props.match.params.id;
    this.setState({mode,curKey});
  }

  componentDidMount(){
    
    pagesRef = firebase.database().ref("pages");
    let { curKey } = this.state;
    
    if(curKey){  // edit or delete
      pagesRef.orderByKey().equalTo(curKey).on("value",(snapshot)=>{
        let data = snapshot.val();
        if(data){
          data = data[curKey]
          this.setState({curPages:data})
        }  
      }, (err)=>{
        console.log("error get key: ",err,curKey)
      })
    }

  }

  addNew() {
    let { curPages } = this.state;
    
    if(this.refs.form){
      this.setState({ redirect: true })
    }  
    pagesRef.push(curPages);
  }
  
  
  updateData(){
    let { curKey, curPages } = this.state;
    
    let updatedData = {[curKey]:curPages};
    if(this.refs.form){
      this.setState({ redirect: true })
    }  
    pagesRef.update(updatedData);
  }
  
  deleteData(){
    let { curKey } = this.state;
    
    if(this.refs.form){
      this.setState({ redirect: true, curKey:null })
    }  
    pagesRef.child(curKey).remove();
  }

  onTypeContent(){
    this.refs.contentMarked.innerHTML = marked(this.refs.contentSource.value);
  }

  render(){
    let { curPages,redirect, mode } = this.state;
    let onclick;
    if(mode === "add"){
      onclick = this.addNew;
    }else if(mode === "edit"){
      onclick = this.updateData;
    }else if(mode === "delete"){
      onclick = this.deleteData;
    }
    
    if(redirect) return <Redirect to={'/pages'} />
    
    console.log("curr:",curPages);
    
    if(!curPages || !mode) return <p>loading..</p>;

    return(
      <div ref="form" onMouseOver={this.onTypeContent}>
        <form className="portfolioForm" onChange={this.updateValues}>
          <div> 
            <span>Title  </span>
            <input id='title' type='text' value = {curPages.title} />
          </div>
          <div>
            <span>Content </span>
            <textarea id='content' 
              onChange = {this.onTypeContent} 
              ref='contentSource' 
              value = {curPages.content} />
            <div className="marked" ref='contentMarked' >
            </div>
          </div>
          <div>
            <span>Hidden </span>
            <select id='hidden' value = {curPages.hidden}>
              <option value={0}>No</option>
              <option value={1}>Yes</option>
            </select>
          </div>
          <div style={{"marginTop":15}}>
            <span>  </span>
            <input type='button' className={`btn ${mode}`} value={mode.toUpperCase()} onClick={onclick} />
          </div>
        </form>
        <Link to={'/pages'}> Cancel </Link>
        
      </div>
    )
  }
}

export default PagesForm;


